<template>
  <div class="login-container">
    <div class="login-bg blur" :style="{backgroundImage: 'url(' + bg + ')'}"></div>
    <div class="loginPanle">
      <el-row class="left">
        <img :src="logo" alt=""/>
      </el-row>
      <el-row class='center'>
        <div>     </div>
      </el-row>
      <el-row class="right">
        <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="login-form" auto-complete="on"
                 label-position="left">
          <el-form-item prop="username" class="input-panel-g">
						<span class="imgbox">
							<img :src="userimg"/>
						</span>
            <el-input ref="username" v-model="loginForm.username" placeholder="" name="username" type="text"
                      tabindex="1" auto-complete="on"/>
          </el-form-item>
          <el-form-item prop="password" class="input-panel-g">
						<span class="imgbox">
							<img class="pwdicon" :src="pwdimg"/>
						</span>
            <el-input :key="passwordType" ref="password" v-model="loginForm.password" :type="passwordType"
                      placeholder="" name="password" tabindex="2" auto-complete="on"
                      @keyup.enter.native="handleLogin"/>
          </el-form-item>
			<el-checkbox label="" v-model="remember"></el-checkbox>
          <el-button :loading="loading" class="loginBtn" @click.native.prevent="handleLogin"> </el-button>
        </el-form>
      </el-row>
    </div>
  </div>
</template>
<script>
  import logo from './assets/img/login-logo.png';
  import userimg from './assets/img/login-username.png';
  import pwdimg from './assets/img/login-password.png';
  import bg from './assets/img/bg.png'
  import Bridge from '@/assets/js/jsBriage.js'

  //import './assets/index.less';
  import request from '@/lib/axios.js';
  import {setUserInfo, setToken, removeToken} from '@/lib/auth';
  import {encryptedData,decrypt} from '@/lib/des-coder.js';

  export default {
    data() {
      const validateUsername = (rule, value, callback) => {
        if (!this.loginForm.username.trim().length > 0) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      const validatePassword = (rule, value, callback) => {
        if (this.loginForm.password.length === 0) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        logo,
        userimg,
        pwdimg,
        bg,
		  remember:false,
        loginForm: {
          username: '',
          password: '',
        },
        loginRules: {
          username: [{required: true, trigger: 'blur', validator: validateUsername}],
          password: [{required: true, trigger: 'blur', validator: validatePassword}],
        },
        passwordType: 'password',
        loading: false,
      }
    },
	  created(){
    	this.loginForm={
			username: localStorage.getItem('LuserName'),
			password: localStorage.getItem('LuserPwd')||null,
		}
		this.remember=localStorage.getItem('remember')==='true'?true:false
	  },
    mounted() {
      Bridge.registerHandler('login', (data) => {
        let obj = JSON.parse(data)
        this.loginForm = obj
        // setTimeout(this.handleLogin,500);
      })
      Bridge.callHandler('mounted', "1", (res) => {

      })
    },
    methods: {
      handleLogin() {
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            removeToken();
			  localStorage.setItem('LuserName',this.loginForm.username)
			  localStorage.setItem('remember',this.remember.toString())
			  if(this.remember){
				  localStorage.setItem('LuserPwd',this.loginForm.password)
			  }else {
				  localStorage.removeItem('LuserPwd')
			  }

			  let obj={
				username:encryptedData(this.loginForm.username),
				password:encryptedData(this.loginForm.password),
			}
            request({
              url: `${this.$ip}/sso/login/loginWithRsa`,
              method: 'post',
              data: obj,
              headers: {
                'Authorization': '',
                'Accept': 'application/json',
              }
            })
              .then((data) => {
                if (data.responseCode == 1000 || data.responseCode == 30002|| data.responseCode == 30010 || data.responseCode == 30003) {
                  setToken(data.data.token);
                  setUserInfo(data.data);
                  this.$router.push({path: '/news'});
                  this.loginSuccess()
                  if( data.responseCode == 30010){
                    this.$message.warning(data.responseMessage);
                  }
                } else {
                  this.$message.error(data.responseMessage||"");
                }
                this.loading = false;
              }).catch((error) => {
              this.$message.error(error.toString());
              this.loading = false;
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      loginSuccess() {
        let _this = this
        Bridge.callHandler('login', _this.loginForm, (res) => {

        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
  .login-container {
    width: 100%;
    height: 100%;
    position: relative;
    .login-bg {
      width: 100%;
      height: 100%;
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      left: 0;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }
    .loginPanle {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 20%;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      .left {
        width: 192px;
        img {
          width: 100%;
        }
      }
      .center {
        font-size: 30px;
        font-family: PingFangSC, PingFangSC-Semibold;
        text-align: center;
        color: #ffffff;
        line-height: 42px;
        margin: 40px 0 30px 0;
      }
      .right {
        .login-form {
          .input-panel-g {
            /deep/ .el-form-item__content {
              width: 272px;
              height: 40px;
              position: relative;
              .imgbox {
                position: absolute;
                left: 8px;
                top: 50%;
                transform: translateY(-50%);
                z-index: 200;
                img {
                  width: 20px;
                }
              }
				input{
					padding-left: 32px;
				}
              /deep/ .el-input {
                height: 100%;
                width: 100%;
                input {
                  height: 100%;
                  width: 100%;
                  font-size: 16px;
                  text-indent: 20px;
                  background: #041252;
                  border: none;
                  color: #fff;
                }
              }
            }
          }
          .loginBtn {
            width: 272px;
            background: #020E49;
            font-size: 20px;
            font-family: PingFangSC, PingFangSC-Semibold;
            text-align: center;
            color: #ffffff;
          }
        }
      }
    }
  }
	/deep/ .el-checkbox{
		margin : 10px 0px 15px 10px ;
		.el-checkbox__inner{
			vertical-align: middle;
			width: 20px;
			height: 20px;
			border-color: rgba(255, 255, 255, 0.45);
			background-color: rgba(255, 255, 255, 0.45);
		}
		.el-checkbox__label{
			color: rgba(255, 255, 255, 0.63);
			font-size: 18px;
			vertical-align: middle;
		}
		.is-checked{
			.el-checkbox__inner{
				border-color:#409eff;
				background-color:#409eff;
			}

		}

		.el-checkbox__inner:after{
			content: '';
			width:7px;
			height:14px;
			text-align: center;
			left: 6px;
			top:0px;
		}


	}
</style>



